@import url(https://fonts.googleapis.com/css?family=Nunito:400,800,900&display=swap);
article, aside, details, figcaption, figure, footer, header, hgroup, nav, section, summary {
  display: block; }

audio, canvas, video {
  display: inline-block; }

audio:not([controls]) {
  display: none;
  height: 0; }

[hidden] {
  display: none; }

html {
  font-family: sans-serif;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%; }

a:focus {
  outline: thin dotted; }

a:active, a:hover {
  outline: 0; }

h1 {
  font-size: 2em; }

abbr[title] {
  border-bottom: 1px dotted; }

b, strong {
  font-weight: 700; }

dfn {
  font-style: italic; }

mark {
  background: #ff0;
  color: #000; }

code, kbd, pre, samp {
  font-family: monospace, serif;
  font-size: 1em; }

pre {
  white-space: pre-wrap;
  word-wrap: break-word; }

q {
  quotes: \201C \201D \2018 \2019; }

small {
  font-size: 80%; }

sub, sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline; }

sup {
  top: -.5em; }

sub {
  bottom: -.25em; }

img {
  border: 0; }

svg:not(:root) {
  overflow: hidden; }

fieldset {
  border: 1px solid silver;
  margin: 0 2px;
  padding: .35em .625em .75em; }

button, input, select, textarea {
  font-family: inherit;
  font-size: 100%;
  margin: 0; }

button, input {
  line-height: normal; }

button, html input[type=button],
input[type=reset], input[type=submit] {
  -webkit-appearance: button;
  cursor: pointer; }

button[disabled], input[disabled] {
  cursor: default; }

input[type=checkbox], input[type=radio] {
  box-sizing: border-box;
  padding: 0; }

input[type=search] {
  -webkit-appearance: textfield;
  box-sizing: content-box; }

input[type=search]::-webkit-search-cancel-button, input[type=search]::-webkit-search-decoration {
  -webkit-appearance: none; }

textarea {
  overflow: auto;
  vertical-align: top; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

body, figure {
  margin: 0; }

legend, button::-moz-focus-inner, input::-moz-focus-inner {
  border: 0;
  padding: 0; }

.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: " ";
  clear: both;
  height: 0; }

*, *:before, *:after {
  box-sizing: border-box; }

html {
  font-size: 10px; }

body {
  font-family: "Nunito", sans-serif; }

h1,
h2,
h3 {
  font-family: "Nunito", sans-serif;
  color: black;
  margin: 0;
  text-transform: uppercase; }

h1 {
  text-transform: none;
  color: white;
  font-size: 7.8rem;
  font-weight: 800;
  letter-spacing: 0.78px;
  max-width: 500px;
  max-height: 150px;
  line-height: 1.03; }

h2 {
  font-size: 2.5rem;
  margin-bottom: 5px;
  text-transform: uppercase;
  font-weight: 900;
  color: #000000;
  padding-top: 30px; }

h3 {
  font-size: 2.4rem;
  margin-bottom: 5px;
  text-transform: uppercase;
  font-weight: 800;
  color: #000000;
  text-align: center;
  letter-spacing: 2.4px; }

p {
  font-size: 1.6rem;
  margin: 2rem auto;
  line-height: 2.8rem; }

header {
  max-width: 100vw;
  max-height: 100px;
  display: flex;
  flex-direction: row;
  justify-content: center; }

ul {
  list-style: none;
  margin: 0;
  padding: 0; }

img {
  margin-left: auto;
  margin-right: auto;
  display: block;
  max-width: 100%; }

a {
  text-decoration: none;
  color: black; }

body {
  background: none; }

button,
button:focus,
button:active,
.filterButton,
.filterButton:focus,
.filterButton:active {
  border: none;
  background: #FF4500;
  outline: none;
  padding: 0; }

button,
.filterButton {
  width: 125px;
  height: 50px;
  position: relative;
  z-index: 2;
  color: white;
  margin: 0 50px;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 1.4rem;
  letter-spacing: 1.4px;
  font-weight: 900; }
  button:before, button:after,
  .filterButton:before,
  .filterButton:after {
    height: 50px;
    width: 50px;
    content: "";
    background: #FF4500;
    display: inline-block;
    position: absolute;
    top: 0;
    z-index: -1; }
  button:before,
  .filterButton:before {
    border-top-left-radius: 50%;
    border-bottom-left-radius: 50%;
    left: -25px; }
  button:after,
  .filterButton:after {
    border-top-right-radius: 50%;
    border-bottom-right-radius: 50%;
    right: -25px; }
  button:hover, button:active,
  .filterButton:hover,
  .filterButton:active {
    color: #FF4500;
    background-color: #FFFFFF;
    border-top: 1px solid #FF4500;
    border-bottom: 1px solid #FF4500;
    height: 50px; }
    button:hover:before, button:hover:after, button:active:before, button:active:after,
    .filterButton:hover:before,
    .filterButton:hover:after,
    .filterButton:active:before,
    .filterButton:active:after {
      background-color: #FFFFFF;
      border-top: 1px solid #FF4500;
      border-bottom: 1px solid #FF4500;
      top: -1px; }
    button:hover:before, button:active:before,
    .filterButton:hover:before,
    .filterButton:active:before {
      border-left: 1px solid #FF4500; }
    button:hover:after, button:active:after,
    .filterButton:hover:after,
    .filterButton:active:after {
      border-right: 1px solid #FF4500; }
  button:active,
  .filterButton:active {
    border-style: none; }

.button {
  font-size: 1.0rem;
  margin: 0 50px; }

.wrapper {
  max-width: 1280px;
  margin: 0 auto; }

@media only screen and (max-width: 1440px) {
  .wrapper {
    max-width: 900px; } }

@media only screen and (max-width: 1024px) {
  .wrapper {
    max-width: 720px; } }

@media only screen and (max-width: 768px) {
  .wrapper {
    max-width: 448px; } }

@media only screen and (max-width: 480px) {
  .wrapper {
    max-width: 300px; } }

.teamList__logo {
  width: 40px; }

.team__logo {
  width: 80px; }

.team__park {
  width: 400px; }

